html {
  box-sizing: border-box;
}

*,
::before,
::after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  text-align: center;
  font-size: 1.2rem;
  color: #021934;
  margin: 0;
}

section {
  padding: 1em;
  margin-bottom: 100px;
}

h1 {
  font-family: "Poppins", sans-serif;
  font-size: 2rem;
}

h2 {
  font-family: "Lora", serif;
  font-size: 1.6rem;
}

h3 {
  font-family: "Lora", serif;
  font-size: 1.4rem;
}

p,
a {
  font-family: "Lora", serif;
  font-weight: 500;
}

table {
  width: 100%;
  border-collapse: collapse;
  font-family: "Lora", serif;
}

th,
td {
  border: 1px solid black;
  padding: 8px;
}

th {
  text-align: left;
  font-weight: bold;
}

th[colspan="2"],
td[colspan="2"] {
  background-color: lightgray;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: "Lora", serif;
  margin-bottom: 5px;
}

.content--warning {
  color: #ff0000;
}

.content--brutal {
  color: transparent;
  cursor: pointer;
}

.origin__text,
.origin__link {
  font-size: 0.9rem;
  color: #021934;
}

.menu__checkbox,
.submenu__checkbox {
  position: absolute;
  left: -99999999px;
}

.menu__list li:first-child > .menu__link {
  background-color: #ff0000;
}

.menu__toggler {
  cursor: pointer;
  font-weight: 700;
  font-size: 2rem;
  display: block;
  padding: 10px;
  background-color: aqua;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.submenu__toggler {
  cursor: pointer;
  display: block;
}

.menu__list,
.submenu__list {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: left;
  background-color: white;
}

/*
