}
*/
.menu__link,
.submenu__link,
.submenu__toggler {
  display: block;
  padding: 1em;
  text-decoration: none;
  color: #021934;
  font-weight: 500;
}

.submenu__link {
  padding-left: 2em;
}

.menu__link:hover,
.submenu__link:hover,
.submenu__toggler:hover {
  font-weight: 700;
  background-color: lightgray;
}

.menu__checkbox:checked ~ .menu__list,
.submenu__checkbox:checked ~ .submenu__list {
  display: block;
}

.header__logo {
  display: none;
}

.menu {
  position: absolute;
  width: 100%;
}

